import React from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import FilmId from "./filmId";
import {withRouter} from "react-router-dom";






class FilmIdContainer extends React.Component {
    componentDidMount() {

    }


    render() {
        return (
            <FilmId id={this.props.match.params.id} filmElem={this.props.filmData}/>)
    }
}
let mapStateToProps = (state) => {
    return {
        filmData: state.Film.filmData
    }
}
let WithRouterFilm = withRouter(FilmIdContainer)

export default compose(
    connect(mapStateToProps , {

    })
)(WithRouterFilm)

